"use client"

import { Card, CardContent } from "@/components/ui/card"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { AlertCircle, CheckCircle2, Info } from "lucide-react"

interface SearchResult {
  type: string
  content: string
  confidence: number
  timestamp: string
}

interface SearchResultsProps {
  results: SearchResult[] | null
  isLoading: boolean
  error?: string
}

export function SearchResults({ results, isLoading, error }: SearchResultsProps) {
  if (error) {
    return (
      <Alert variant="destructive" className="mb-4">
        <AlertCircle className="h-4 w-4" />
        <AlertTitle>Error</AlertTitle>
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    )
  }

  if (isLoading) {
    return (
      <div className="space-y-4">
        {[...Array(3)].map((_, i) => (
          <Card key={i} className="overflow-hidden">
            <CardContent className="p-4">
              <div className="flex items-center gap-2 mb-3">
                <div className="h-4 w-4 bg-muted rounded-full animate-pulse"></div>
                <div className="h-4 w-32 bg-muted rounded animate-pulse"></div>
              </div>
              <div className="space-y-2">
                <div className="h-3 w-3/4 bg-muted rounded animate-pulse"></div>
                <div className="h-3 w-1/2 bg-muted rounded animate-pulse"></div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    )
  }

  if (!results || results.length === 0) {
    return (
      <Alert className="mb-4">
        <Info className="h-4 w-4" />
        <AlertTitle>No Results</AlertTitle>
        <AlertDescription>
          No matches found. Try adjusting your search criteria or try a different search term.
        </AlertDescription>
      </Alert>
    )
  }

  return (
    <div className="space-y-4">
      {results.map((result, index) => (
        <Card key={index} className="overflow-hidden hover:shadow-md transition-shadow">
          <CardContent className="p-4">
            <div className="flex items-start justify-between">
              <div className="flex-1">
                <div className="flex items-center gap-2 mb-2">
                  <CheckCircle2 className={`h-4 w-4 ${
                    result.confidence >= 90 ? 'text-green-500' :
                    result.confidence >= 70 ? 'text-yellow-500' :
                    'text-red-500'
                  }`} />
                  <span className="font-medium text-foreground">{result.type}</span>
                </div>
                <p className="text-sm text-muted-foreground">{result.content}</p>
              </div>
              <div className="text-right ml-4">
                <div className={`text-sm font-medium ${
                  result.confidence >= 90 ? 'text-green-500' :
                  result.confidence >= 70 ? 'text-yellow-500' :
                  'text-red-500'
                }`}>
                  {result.confidence}% Match
                </div>
                <div className="text-xs text-muted-foreground mt-1">
                  {result.timestamp}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      ))}
    </div>
  )
}
